












import { Component, Vue } from 'vue-property-decorator'
import { VueAmap } from '../../packages'
import { GeolocationOptions } from '@vita2333/vue-amap2.0'
import DemoBlock from '@/components/DemoBlock.vue'

@Component({
  components: { DemoBlock, VueAmap },
})
export default class GetPositionDemo extends Vue {
  geolocationOptions1: GeolocationOptions = {
    enableHighAccuracy: true, // 是否使用高精度定位，默认:true
    timeout: 10000,           // 超过10秒后停止定位，默认：5s
    buttonPosition: 'RB',     // 定位按钮的停靠位置
    zoomToAccuracy: true,    // 定位成功后是否自动调整地图视野到定位点
  }
  geolocationOptions2: GeolocationOptions | null = null

  created () {
    this.$amapLoader().then((AMap) => {
      this.geolocationOptions2 = {
        showButton: true, // 是否显示定位按钮
        buttonPosition: 'LB', // 定位按钮的位置
        /* LT LB RT RB */
        buttonOffset: new AMap.Pixel(10, 20), // 定位按钮距离对应角落的距离
        showMarker: true, // 是否显示定位点
        markerOptions: { // 自定义定位点样式，同Marker的Options
          offset: new AMap.Pixel(-18, -36),
          content:
            '<img alt="icon" src="https://a.amap.com/jsapi_demos/static/resource/img/user.png" style="width:36px;height:36px"/>',
        },
        showCircle: true, // 是否显示定位精度圈
        circleOptions: { // 定位精度圈的样式
          strokeColor: '#0093FF',
          noSelect: true,
          strokeOpacity: 0.5,
          strokeWeight: 1,
          fillColor: '#02B0FF',
          fillOpacity: 0.25,
        },
      }
    })
  }

}
